/* Tooltip Container */
.tooltip-container {
  position: relative;
  display: inline-block;
}

/* Tooltip Content */
.tooltip {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 5px;
  border-radius: 3px;
  font-size: 14px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
  white-space: nowrap;
}

/* Show Tooltip on Hover */
.tooltip-container:hover .tooltip {
  opacity: 1;
  visibility: visible;
}
