div.pulldown {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: auto;
    padding: 10px 30px 10px 20px;
    margin-top: 0;
    font-weight: 600;
    color: #30475e;
    background-color: #f5f5f5;
    border-radius: 5px;
    box-shadow: 0 0 10px 2px rgb(0 0 0 / 30%);

    > a {
        color: inherit;
        text-decoration: none;

        > span {
            display: flex;
            align-items: center;
            padding: 10px;
            white-space: nowrap;
        }

        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
    }

}
