.header {
    position: fixed;
    z-index: 999;
    display: flex;
    width: 100%;
    top: 0;
    opacity: 1;
    transition: opacity 0.5s ease;

    > a > img.logo {
        width: 120px;
        margin: 5px 0 0 150px;
    }

    > .tab-wrapper {
        position: relative;
        margin-right: 8%;

        &:first-of-type {
            margin-left: 15%;
        }

        > .tab {
            display: flex;
            width: 100%;
            padding-top: 40px;
            padding-bottom: 5px;
            font-family: Maven+Pro;
            font-size: 18px;
            color: #f5f5f5;

            &:hover {
                color: #f05454;
                cursor: grab;
            }

            > a {
                width: 100%;
                font-family: Maven+Pro;
                text-decoration: none;
                white-space: nowrap;
                color: #f5f5f5;

                &:hover {
                    color: #f05454;
                    cursor: grab;
                }

                > .icon.external-link {
                    position: relative;
                    padding-left: 5px;
                    top: 3px;
                }
            }
        }
    }
}

.header.hide {
    pointer-events: none;
    opacity: 0;
}

div.empty-box {
    margin-top: 140px;
}